<template>
    <div class="sworks">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-calendar"></i> 学生管理
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="form"> 
                <div class="form-top">
                    <el-button class="fr downloadbutton" type="primary" size="small"><a target="_blank" style="color:#fff;" :href="downurls+'/template/学生模板.xls'">下载用户模板</a></el-button>
                    <a class="file fr">导入学生用户<input class="upload" @change='add_img' :value="filevalue" type="file"></a>
                    <el-button class="fr" type="primary" @click="newstudent" size="small">创建学生用户</el-button>
                </div>
                <div class="cont" v-loading="loading">
                    <el-table
                      :data="tableData"
                      style="width: 100%;margin-top:20px;">
                      <el-table-column
                        type="index"
                        width="100px"
                        label="序号">
                      </el-table-column>
                      <el-table-column
                        prop="number"
                        label="学号">
                      </el-table-column>
                      <el-table-column
                        prop="name"
                        label="姓名">
                      </el-table-column>
                      <el-table-column
                        prop="enrollmentYear"
                        label="入学年份">
                      </el-table-column>
                      <el-table-column
                        prop="academyName"
                        label="院系">
                      </el-table-column>
                      <el-table-column
                        prop="className"
                        label="班级">
                      </el-table-column>
                      <el-table-column label="操作" width='150px'>
                          <template slot-scope="scope">
                            <el-button
                              size="mini"
                              @click="handleEdit(scope.row)">编辑</el-button>
                            <el-button
                              size="mini"
                              type="danger"
                              @click="delworks(scope.row.id)">删除</el-button>
                          </template>
                        </el-table-column>
                    </el-table>

                    <div class="pagination">
                        <el-pagination
                          background
                          :page-size="pageSize"
                          @current-change="changePage"
                          layout="prev, pager, next"
                          :total="total">
                        </el-pagination>
                    </div>
                    
                </div>
            </div>
        </div>
        <!-- 添加弹出框 -->
        <el-dialog title="添加学生" class="datas-bg" :visible.sync="editVisible" width="380px">
            <el-input class="margin10" v-model="number" size="small" placeholder="请输入学号"></el-input>
            <el-input class="margin10" v-model="name" size="small" placeholder="请输入姓名"></el-input>
            <el-select class="margin10" v-model="enrollmentId" size="small" placeholder="请选择入学年份">
              <el-option
                v-for="item in allenrollmentYear"
                :key="item.id"
                :label="item.enrollmentYear"
                :value="item.id">
              </el-option>
            </el-select><br />
            <el-select class="margin10" @change="Getclassbyacademyid()" v-model="academyId" size="small" placeholder="请选择院系">
              <el-option
                v-for="item in allacademy"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select><br />
            <el-select class="margin10" :disabled="allclass.length==0" v-model="classId" size="small" placeholder="请选择班级">
              <el-option
                v-for="item in allclass"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select><br />
            <el-button type="primary" style="display:block;margin:0 auto;" size="small" @click="Addstudent">确 定</el-button>
        </el-dialog>
        <!-- 编辑弹出框 -->
        <el-dialog title="编辑学生" class="datas-bg" :visible.sync="editVisible1" width="380px">
            <el-input class="margin10" v-model="number" size="small" placeholder="请输入学号"></el-input>
            <el-input class="margin10" v-model="name" size="small" placeholder="请输入姓名"></el-input>
            <el-select class="margin10" v-model="enrollmentId" size="small" placeholder="请选择入学年份">
              <el-option
                v-for="item in allenrollmentYear"
                :key="item.id"
                :label="item.enrollmentYear"
                :value="item.id">
              </el-option>
            </el-select><br />
            <el-select class="margin10" @change="Getclassbyacademyid()" v-model="academyId" size="small" placeholder="请选择院系">
              <el-option
                v-for="item in allacademy"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select><br />
            <el-select class="margin10" :disabled="allclass.length==0" v-model="classId" size="small" placeholder="请选择班级">
              <el-option
                v-for="item in allclass"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select><br />
            <el-button type="primary" style="display:block;margin:0 auto;" size="small" @click="Updatestudent">确 定</el-button>
        </el-dialog>
    </div>
</template>

<script>
import {fetchListstudent, fetchListallenrollmentyear, fetchAddstudent, fetchDeletestudent, fetchGetallacademylist, fetchGetclassbyacademyid, fetchUpdatestudent} from "../../axios/requests";
import {post3, downurl} from '../../axios/index'
export default {
    name: 'course',
    data() {
        return {
            editVisible:false,
            editVisible1:false,
            pageNum:1,
            pageSize:10,
            total:0,
            loading:false,

            allenrollmentYear:[],
            enrollmentId:'',
            allacademy:[],
            academyId:'',
            allclass:[],
            classId:'',
            classId2:'',
            number:'',
            name:'',
            id:'',

            editItems:'',

            tableData: [],
            downurls:'',
            filevalue:'',
        };
    },
     mounted(){
        this.Liststudent();
        this.Listallenrollmentyear();
        this.Getallacademylist();
        this.downurls = downurl;
    },
    methods: {
        async add_img(event){ 
          const loading = this.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading'
          });
          let reader =new FileReader();
          let img1=event.target.files[0];
          let form = new FormData(); 
          form.append('file',img1,img1.name);
          try {
            let response = await post3('/admin/importstudent/',form);
            if (response.status.code ==0) {
              this.pageNum = 1;
              this.$message({
                message: '上传成功',
                type: 'success'
              });
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) { 
            console.log(e);
          } finally {
            loading.close()
            this.filevalue = '';
          } 
        },

        openLink(){
            this.$router.push({path:'newworks'})
        },
        handleEdit(e){
            this.editVisible1 = true;
            this.number = e.number;
            this.name = e.name;
            this.enrollmentId = e.enrollmentId;
            this.academyId = e.academyId;
            this.classId2 = e.classId;
            this.id = e.id;
            this.Getclassbyacademyid();
        },
        delworks(id){
            this.$confirm('确定要删除吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                this.Deletestudent(id)
            })
            .catch(() => {});
        },
        changePage(page){
          this.pageNum = page;
          this.Liststudent();
        },
        newstudent(){
          this.Listallenrollmentyear();
          this.Getallacademylist();
          this.editVisible=true
          this.number = "";
          this.name = "";
          this.enrollmentId = "";
          this.academyId = "";
          this.classId2 = "";
          this.classId = "";
        },
        async Liststudent() {//得到老师列表
            this.loading = true;
            let {pageNum, pageSize} = this;
            try {
              let response = await fetchListstudent(pageNum, pageSize);
              if (response.status.code ==0) {
                this.total = response.result.total;
                this.tableData = response.result.list;
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              this.loading = false;
            }
        },
        async Updatestudent(){
          let {number, name, academyId, enrollmentId, classId, id} = this;
            if (!number) {this.$message.error('学号不能为空');return};
            if (!name) {this.$message.error('姓名不能为空');return};
            if (!academyId) {this.$message.error('请选择入学年份');return};
            if (!enrollmentId) {this.$message.error('请选择院系');return};
            if (!classId||classId=="该学院下暂无班级") {this.$message.error('请选择班级');return};
             const loading = this.$loading({
              lock: true,
              text: '加载中...',
              spinner: 'el-icon-loading'
            });
            try {
              let response = await fetchUpdatestudent(id, number, name, academyId, enrollmentId, classId);
              if (response.status.code == 0) {
                  this.$message({
                    message: '修改成功',
                    type: 'success'
                  });
                  this.editVisible1 = false;
                this.Liststudent()
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              loading.close()
            }
        },
        async Addstudent() {//添加学生
          let {number, name, academyId, enrollmentId, classId} = this;
            if (!number) {this.$message.error('学号不能为空');return};
            if (!name) {this.$message.error('姓名不能为空');return};
            if (!academyId) {this.$message.error('请选择入学年份');return};
            if (!enrollmentId) {this.$message.error('请选择院系');return};
            if (!classId||classId=="该学院下暂无班级") {this.$message.error('请选择班级');return};
             const loading = this.$loading({
              lock: true,
              text: '加载中...',
              spinner: 'el-icon-loading'
            });
            try {
              let response = await fetchAddstudent(number, name, academyId, enrollmentId, classId);
              if (response.status.code ==0) {
                this.$message({
                  message: '添加成功',
                  type: 'success'
                });
                this.editVisible = false;
                this.Liststudent()
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              loading.close()
            }
        },
        async Deletestudent(id) {//删除学生
            const loading = this.$loading({
              lock: true,
              text: '加载中...',
              spinner: 'el-icon-loading'
            });
            try {
              let response = await fetchDeletestudent(id);
              if (response.status.code ==0) {
                this.$message({
                  message: '删除成功',
                  type: 'success'
                });
                this.Liststudent();
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              loading.close()
            }
        },
        async Listallenrollmentyear() {//得到所有入学年份列表
            try {
              let response = await fetchListallenrollmentyear();
              if (response.status.code ==0) {
                this.allenrollmentYear = response.result.list;
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              this.loading = false;
            }
        },
        async Getallacademylist() {//得到所有学院列表
            try {
              let response = await fetchGetallacademylist();
              if (response.status.code ==0) {
                this.allacademy = response.result.list;
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              this.loading = false;
            }
        },
        async Getclassbyacademyid() {//得到学院下的班级
            try {
              let response = await fetchGetclassbyacademyid(this.academyId);
              if (response.status.code ==0) {
                if (response.result.list.length==0) {
                  this.classId2 = "该学院下暂无班级"
                }else{
                  this.classId = ""
                  if (this.classId2=="该学院下暂无班级") {
                    this.classId2=""
                  };
                }
                this.allclass = response.result.list;
                this.classId = this.classId2;
              }else{
                this.$message.error(response.status.description);
              }
            } catch (e) {
              console.log(e);
            } finally {
              this.loading = false;
            }
        },
    }
};
</script>
<style lang='scss'>
    .datas-bg{
         .el-dialog__body{
            padding:10px 20px 30px;
         }
    }
</style>
<style lang='scss' scoped>
    .sworks{
        .form-top{
            overflow:hidden;
        }
    }
    .pagination{
        margin-top:20px;
    }
    .downloadbutton{
      margin-left:10px;
    }
    .margin10{
      margin-bottom:10px;
      width:100%;
    }
     .file {
        width:104px;
        height:30px;
        cursor:pointer;
        position: relative;
        display: inline-block;
        background: #409EFF;
        border: 1px solid #99D3F5;
        border-radius: 4px;
        overflow: hidden;
        color: #fff;
        text-decoration: none;
        text-indent: 0;
        line-height: 30px;
        overflow:hidden;
        font-size:12px;
        text-align:center;
        margin-left:10px;
    }
    .file input {
        position: absolute;
        width:104px;
        height:30px;
        font-size: 12px;
        right: 0;
        top: 0;
        opacity: 0;
        cursor:pointer;
    }
    .file:hover {
        background: #66b1ff;
        border-color: #66b1ff;
        color: #fff;
        text-decoration: none;
    }
</style>


